    CargaInicioSesion(): boolean {
        if (localStorage.getItem("currentPaciente")) {
            this.objUser = JSON.parse(localStorage.getItem("currentPaciente"));
            demo.Alerta("Bienvenido a AlertaTuLlave App", this.objUser.displayName, "success");
            return true;
        }
        else {
            //Control de seguridad
            this.router.navigate(['/security/login']);
            return false;
        }
    }

    //Inicializa proyectos
    CargarProyectos() {
        var x = this.dbProyectos.getData(this.objUser.email.replace(".", "-"));
        x.snapshotChanges().subscribe(item => {
            this.itemProyectos = [];
            item.forEach(element => {
                var y = element.payload.toJSON();

                y["$key"] = element.key;//identificador proyecto


                //identificador proyecto
                y["$key"] = element.key;

                y["Color"] = ((y["Valor"] as number) < 4800 ? 'rojo' : 'normal');
                y["Descripcion"] = ((y["Valor"] as number) < 4800 ? 'Saldo proximo a agotarse, por favor recarga tu tarjeta.' : 'Saldo disponible.');
                //Fin validacion likes
                y["TotalPasajes"] = ((y["Valor"] as number) > 2300 ? (Math.round((y["Valor"] as number) / 2400)) : 0);
                this.itemProyectos.push(y as PacientesModel);

            });

        });

    }

