import { Injectable } from '@angular/core';
import { AngularFireDatabase, AngularFireList } from '@angular/fire/database';
import { CalendarModel } from '../model/Calendar.model';
import * as firebase from 'firebase';

@Injectable()
export class CalendarService {
  public itemRef: AngularFireList<any>;

  constructor(private db :AngularFireDatabase) {}

  //$($('td[class*="fc-day"]')[0]).attr("data-date")
  getData(FiltroDiaSemana: string,Nit: string){
    this.itemRef = this.db.list(`Calendar/${FiltroDiaSemana}/${Nit}`);
    return this.itemRef;
  }

  getDataKey(FiltroDiaSemana: string,Nit: string, key: string){
    this.itemRef = this.db.list(`Calendar/${FiltroDiaSemana}/${Nit}/${key}`);
    return this.itemRef;
  }

  insert(FiltroDiaSemana: string,Nit: string, calendario : CalendarModel): string
  {
    const itemPath =  `Calendar/${FiltroDiaSemana}/${Nit}`;
    return this.db.list(itemPath).push(calendario).key;
  }

  delete(FiltroDiaSemana: string,Nit: string, key : string)
  {
    const itemPath =  `Calendar/${FiltroDiaSemana}/${Nit}`;
    return this.db.list(itemPath).remove(key);
  }

  update(FiltroDiaSemana: string,Nit: string, calendario : CalendarModel) {
    const itemPath =  `Calendar/${FiltroDiaSemana}/${Nit}/${calendario.$key}`;
    
    firebase.database().ref(itemPath).update({
      id: calendario.id,
      title : calendario.title,
      start: calendario.start,
      end : calendario.end,
      date: calendario.date,
      url: calendario.url,
      allDay: calendario.allDay,
      className: calendario.className,
      FechaModificacion:  new Date().toISOString(),
      Estado : calendario.Estado
    });
  }

}