    //Metodos
    //Valida autenticacin
    CargaInicioSesion(): boolean {
        if (localStorage.getItem("currentPaciente")) {
            this.objUser = JSON.parse(localStorage.getItem("currentPaciente"));
            this.item_Form = new PacientesModel();
            this.item_Form.Correo = this.objUser.email;
            return true;
        }
        else {
            //Control de seguridad
            this.router.navigate(['/security/login']);
            return false;
        }
    }

    ngOnInit() {

        $().ready(function () {

            //Inicio - posicin inicial
            let body = $(".main-panel");
            let top = body.scrollTop() // Get position of the body
            if (top != 0) { body.animate({ scrollTop: 0 }, '1500'); }
            //Fin - posicion inicial
        });
    }

    selectFile(event) {
        const file = event.target.files.item(0)

        if (file.type.match('image.*')) {
            this.selectedFiles = event.target.files;
        } else {
            alert('invalid format!');
        }
    }

    CambioEstado(value: string) {
        if (value == "true") { value = "false"; } else { value = "true"; }
        this.item_Form.Estado = value;
    }

