    //Inicializa proyectos
    BuscaTarjetaUsuario(tarjeta: string) {
        if (tarjeta != null) {
            var strCorreo: string = this.objUser.email;
            var x = this.dbPaciente.getData(strCorreo.replace(".", "-"));
            x.snapshotChanges().subscribe(item => {

                this.operacion = false;

                item.forEach(element => {
                    this.item_Form = new PacientesModel;
                    var y = element.payload.toJSON();

                    y["$key"] = element.key;//identificador proyecto

                    if (y["NumeroTarjeta"] == tarjeta) {
                        this.item_Form = y as PacientesModel;
                        this.operacion = true;
                    }

                });
                
                this.item_Form.NumeroTarjeta = tarjeta;
                this.item_Form.Correo = strCorreo;
                this.item_Form.Valor = (this.item_Form.Valor != "" ? this.item_Form.Valor : "0");
            });

        }
    }

    ModificarPaciente() {

        if (this.operacion) {
            this.dbPaciente.update(this.item_Form);
        }
        else {
            this.item_Form.Valor = "0";
            this.dbPaciente.insert(this.item_Form);
        }

        demo.Alerta("Tarjeta", "Se ha creado la tarjeta correctamente.", "success");

    }

