import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from '@angular/common';

@Component({
    moduleId: module.id,
    selector: 'footer-cmp',
    templateUrl: 'footer.component.html'
})

export class FooterComponent{
    location: Location;
    test : Date = new Date();

    constructor(public router: Router,location:Location) { this.location = location; }




    public isFullScreen():boolean{
        if(this.location.prepareExternalUrl(this.location.path()).toLowerCase().substring(0,17) == '#/fullscreen'){
            return false;
        }
        else {
            return true;
        }
    }

}
