import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import * as moment from 'moment'; // add this 1 of 4
import { UsuarioModel } from "../../../../model/usuario.model";
import { User } from '../../../../model/user.model';
import { UsuarioService } from '../../../../services/usuario.services';
import { CalendarModel } from '../../../../model/Calendar.model';
import { CalendarService } from '../../../../services/calendar.services';

declare var $: any;
@Component({
    moduleId: module.id,
    selector: 'calendar-cmp',
    templateUrl: 'calendar.component.html',
    providers: [
        CalendarService,
        UsuarioService,
    ],
})

export class CalendarComponent {

    public objUser = User;
    public objIdentificacion: any; // verifica si el paciente cuenta con identificacion
    public dataCalendar: any[];
    public urlNuevoProyecto: string = "../../forms/extended";

    public itemProfesionales: UsuarioModel[];
    public item_Profesional: UsuarioModel;

    constructor(private router: Router, private dbCalendar: CalendarService, private dbUsuario: UsuarioService) {
        this.CargaInicioSesion();
    }


    //Valida autenticacin
    CargaInicioSesion(): boolean {
        if (localStorage.getItem("currentPaciente")) {
            this.objUser = JSON.parse(localStorage.getItem("currentPaciente"));
            return true;
        }
        else {
            //Control de seguridad
            this.router.navigate(['/security/login']);
            return false;
        }
    }

    CargaCalendario() {
        this.dataCalendar = [];
        $('#uiCalendar').fullCalendar('removeEvents');

        let strFechaFiltro = $($('td[class*="fc-day"]')[0]).attr("data-date");

        let iCount = 0;
        while (this.objUser.CentroMedico[iCount] != undefined) {

            let x = this.dbCalendar.getData(strFechaFiltro, this.objUser.CentroMedico[iCount]);
            x.snapshotChanges().subscribe(item => {

                item.forEach(element => {
                    var y = element.payload.toJSON();

                    if (y["IdentificacionPaciente"] == this.objUser.Documento) {

                        y["$key"] = element.key;//identificador proyecto

                        if (y["estado"] == "1") {

                            var check = moment(y["start"]).format('YYYY-MM-DD');
                            var today = moment(new Date()).format('YYYY-MM-DD');

                            // si el inicio de evento ocurre hoy o en el futuro mostramos el modal
                            if (check < today) {
                                y["className"] = "event-default"; //cita que el paciente no asistio
                                y["estado"] = 99;
                            }
                            else if (check == today) {
                                y["className"] = "event-orange";
                            }

                        } else if (y["estado"] == "2") {
                            y["className"] = "event-azure";
                        }

                        this.dataCalendar.push(y as CalendarModel);
                        $('#uiCalendar').fullCalendar('addEventSource', this.dataCalendar);
                    }

                });

            });

            iCount++;
        }

    }

    CancelarCita() {
        //this.LimpiarControles();
    }

    ngOnInit() {

        $().ready(function () {

            //Inicio - posicin inicial
            let body = $(".main-panel");
            let top = body.scrollTop() // Get position of the body
            if (top != 0) { body.animate({ scrollTop: 0 }, '1500'); }
            //Fin - posicion inicial

            $('.selectpicker').selectpicker('refresh');
        });

        var today = new Date();
        var y = today.getFullYear();
        var m = today.getMonth();
        var d = today.getDate();


        $('#uiCalendar').fullCalendar({  // assign calendar
            header: {
                left: 'prev,next today',
                center: 'title',
                right: 'agendaWeek'
            },
            dayNames: ['Domingo', 'Lunes', 'Martes', 'Mircoles', 'Jueves', 'Viernes', 'Sbado'],
            dayNamesShort: ['Do', 'Lu', 'Ma', 'Mi', 'Ju', 'Vi', 'S'],
            monthNames: ['Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre'],
            monthNamesShort: ['Ene', 'Feb', 'Mar', 'Abr', 'May', 'Jun', 'Jul', 'Ago', 'Sep', 'Oct', 'Nov', 'Dic'],
            buttonText: {
                today: 'Hoy',
                month: 'Mes',
                week: 'Semana',
                day: 'Da'
            },
            defaultView: 'agendaWeek',
            editable: false,
            selectable: true,
            overlap: false,
            events: [],

            viewRender: function (view) {
                // We make sure that we activate the perfect scrollbar when the view isn't on Month
                if (view.name != 'month') {
                    var $fc_scroller = $('.fc-scroller');
                    $fc_scroller.perfectScrollbar();
                }
                let a=0 ;
                $('.fc-unthemed button.fc-button[type=button]').click(function (ei) {
                    $("#btnCalendarRefrest").trigger("click");
                    console.log(a++);
                });
            },

            eventRender: function (event, element) {
                if (event.estado == 1) {

                    var check = moment(event.start).format('YYYY-MM-DD');
                    var today = moment(new Date()).format('YYYY-MM-DD');

                    // si el inicio de evento ocurre hoy o en el futuro mostramos el modal
                    if (check < today) {
                        //element.css('color', 'blue');
                        element.css('background-color', '#715554');
                        event.estado = 99;
                    }

                }
            },

            eventClick: function (event) {  // when some one click on any event
                let endtime = moment(event.end).format('h:mm');
                let starttime = moment(event.start).format('dddd, MMMM D YYYY, h:mm');
                var mywhen = starttime + ' - ' + endtime;
                $('#modalTitle').html(event.title);
                $("#txtIdentificacionPaciente").html("IDENTIFICACIN: " + event.IdentificacionPaciente);
                $("#txtNombrePaciente").html("PACIENTE: " + event.NombrePaciente);
                $("#txtNombreProfesional").html("PROFESIONAL: " + event.Profesional);
                $('#modalWhen').text(mywhen);
                $('#eventID').val(event.$key);

                $('#calendarModal').modal();
            },

            select: function (start, end, jsEvent) {  // click on empty time slot                
                //Limpia controles
                $('#txtDocumento').val('');
                this.item_Profesional = new UsuarioModel;
                setTimeout(function () { $('.selectpicker').selectpicker('refresh'); }, 1000);
                //Fin limpiar              

                let endtime = moment(end).format('h:mm');
                let starttime = moment(start).format('dddd, MMMM D YYYY, h:mm');
                var mywhen = starttime + ' - ' + endtime;

                $('#lblMinutos').text(mywhen);
                $('#createEventModal #startTime').val(start);
                $('#createEventModal #endTime').val(end);
                $('#createEventModal #when').text(mywhen);
                $('#createEventModal').modal();
            },
        });

        this.CargaCalendario();

    }


    doDelete() {  // delete event 
        $("#calendarModal").modal('hide');
        let eventID = $('#eventID').val();
        let strFechaFiltro = $($('td[class*="fc-day"]')[0]).attr("data-date");
        this.dbCalendar.delete(strFechaFiltro, this.objUser.Documento, eventID);
    }



    ngAfterViewInit() {

    }

    onCalendarInit(initialized: boolean) {
        console.log('Calendar initialized');
    }
}
