import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { PacientesService } from '../../../../services/pacientes.services'

//validaciones y conexion
import * as firebase from 'firebase';
import { EmailValidator } from '../../../../validators/email';



declare var $:any;
declare var demo:any;
declare var mdp:any;
@Component({
  selector: 'app-new',
  templateUrl: './new.page.html',
  styleUrls: ['./new.page.scss'],
  providers :[
    PacientesService,
  ],
})
export class NewPage implements OnInit {
  constructor(private router: Router, private dbPaciente: PacientesService) { }

  txtEmail:'';
  ddlTipoIdentificacion: string;
  txtDocumento:'';
  txtPassword:'';
  uiCheck :'';
  txtConfirmarPassword :'';

  SelecTipoIdentificacion(id: string) {
    this.ddlTipoIdentificacion = id;
  }

  ngOnInit() {
    $(".plt-desktop").removeClass('nav-open');
    $(".plt-mobile").removeClass('nav-open');  
    $('.main-panel').addClass('main-panel-security');
    $('.main-panel-security').removeClass('main-panel');

    $.getScript('./assets/js/demo.js');
    $.getScript('./assets/js/init/initMenu.js');


    $().ready(function() {      

      //Efectomivimiento en imagen
      demo.checkFullPageBackgroundImage();

      setTimeout(function() {
          // after 1000 ms we add the class animated to the login/register card
          $('.card').removeClass('card-hidden');
      }, 700)
      
      $('ion-app > div.wrapper').perfectScrollbar();
      
      $('ion-app > div.wrapper').perfectScrollbar();
      
      $('.selectpicker').selectpicker('refresh');
      
      //Actualiza control del menu
      try{ if(mdp){ mdp.initSidebarsCheck(); }}catch(e){ }
    });
  }

  getLoginUser(){
    return "./#/security/login";
  }
  getLockUser(){
      return "./#/security/lock";
  } 

  getValidarUsers(){
    if(this.txtPassword == this.txtConfirmarPassword){

        if(EmailValidator.isValid(this.txtEmail)){

        //Ejecuta metodo evento promesa
        firebase.auth().createUserWithEmailAndPassword(this.txtEmail, this.txtPassword).then(authData => {

          //Almacena la imagen asociada con el key
          //this.dbPaciente.insert({
          //  NumeroTarjeta:'',
          //  Nombres:'Usuario',
          //  Apellidos:'',
          //  Correo: authData.user.email,
          //  Celular: '',
          //  Estado: '1',
          //  Valor:'0'
          //});

          demo.Alerta("Usuario", "Se ha creado correctamente.", "success");
            

          //lineas para eliminar sesion del usuario
          localStorage.clear();  

          this.router.navigate(['/security/login']);

        }, error => {
          demo.Alerta("Creacin", "datos invalidos, verifique e intente nuevamente.", "warning");
          localStorage.clear();
        }); 
     
        

      }
      else{
        demo.Alerta("Creacin paciente", "Correo invalido, verifique e intente nuevamente.", "warning");
      }
    }
    else{
      demo.Alerta("Creacin paciente", "Confirmacin de password invarido verifique e intente nuevamente.", "warning");
    }
  }

}
